@import "../mixins";
.blog {
  &__header {
    margin-bottom: 1rem;

    text-align: center;
  }

  &__title {
    margin-bottom: 0.1em;
  }

  &__time {
    font-style: italic;
  }

  &__text {
    margin-bottom: 0.25em;

    font-size: 1.5rem;
    text-align: justify;
  }

  &__author {
    text-align: right;
    font-style: italic;
    color: rebeccapurple;
    margin-bottom: 1em;
  }

  &__btn {
  }
}
