@import "../mixins";
.create {
  text-align: center;
  &__title {
    margin-bottom: 0.2em;
  }
  &__form {
    padding: 0;
    max-width: $normal;

    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__label {
    font-family: "Dancing Script", cursive;
    font-size: 2rem;
    &._wide {
      width: 100%;
    }
  }

  &__field,
  &__area {
    padding: 0.6em 0.5em;
    margin-top: 0.5em;
    margin-bottom: 1em;

    font-size: 1.4rem;
    font-family: "Roboto", sans-serif;

    background-color: whitesmoke;
    border-radius: 0.25em;

    &:focus {
      border: thin solid $bg-primary;
      box-shadow: $bg-primary 0 0 0.25em;
    }
  }
  &__field {
    text-align: center;
  }

  &__area {
    width: 100%;
    resize: none;
    height: 12em;
  }

  &__btn {
    font-size: 1.5rem;
    &:focus-visible {
      border: thin solid $bg-primary;
      box-shadow: $bg-primary 0 0 0.25em;
    }
  }
}
