@import "_mixins";

$barheight: 80px;
$time: 0.3s;

.container {
  margin: 0 auto;
  padding: 0 0.75rem;
}

.main {
  &__container {
    padding-top: 2rem;
    padding-bottom: 2rem;
    max-width: $large;
  }
}
.title {
  font-family: "Dancing Script", cursive;
  font-weight: 700;
  font-size: 3.5rem;
  color: $bg-primary;
}

.btn {
  display: inline-block;
  padding: 0.6em 0.5em;

  color: white;

  background-color: $bg-secondary;
  border-radius: 0.25em;
  transition: $time;
  &:hover {
    background-color: $color-secondary;
  }
  &:active {
    transform: translateY(5%);
  }
}

.wrapper {
  min-height: 100vh;
}
.navbar {
  background-color: palevioletred;
  &__container {
    max-width: $large;
    min-height: $barheight;

    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__brand {
    display: flex;
    align-items: center;

    font-size: 2.5rem;
    font-family: "Dancing Script", cursive;
    font-weight: 700;
    color: white;

    transition: $time;
    &:hover {
      color: aquamarine;
    }
  }
  &__brand:hover &__svg {
    fill: aquamarine;
  }
  &__svg-layout {
    margin-right: 0.1em;

    height: #{$barheight * 0.8};
    width: #{$barheight * 0.8};

    display: flex;
    align-items: center;
    justify-content: center;

    border-radius: 50%;
    background-color: plum;
    @include media-wp($x-small) {
      margin-right: 0.5em;
    }
  }
  &__svg {
    width: 60%;
    height: 60%;

    fill: white;

    transition: $time;
  }

  &__nav {
  }
}
.nav {
  &__list {
    display: flex;
    align-items: center;
  }

  &__item {
    margin-right: 1em;
  }

  &__link {
    font-size: 1.2rem;
    color: white;
    transition: 0.3s;
    &:hover {
      color: $color-primary;
    }
  }
}
