@import "../mixins";
.blogs {
  &__list {
  }

  &__item {
    margin-bottom: 1em;
  }

  &__link {
    display: block;
    padding: 0.75rem;

    font-size: 1.5rem;

    background-color: aquamarine;
    border-radius: 0.75rem;
    transition: 0.3s;
    &:hover {
      background-color: aqua;
    }
  }

  &__title {
    margin-bottom: 0.5rem;

    color: $bg-primary;
    font-size: 2.5rem;
    font-family: "Dancing Script", cursive;
    transition: 0.3s;
  }

  &__time {
    transition: 0.3s;
    font-size: 1.25rem;
    color: $color-secondary;
  }
}
