@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400;700&display=swap);
*,*:before,*:after{box-sizing:border-box}*{padding:0;margin:0;border:0}:focus,:active{outline:none}a:focus,a:active{outline:none}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}html:focus-within{scroll-behavior:smooth}body{min-height:100vh;font-size:100%;line-height:1;vertical-align:baseline}picture,img{display:inline-block;vertical-align:top}input,button,textarea,select{font-family:inherit}input::-ms-clear{display:none}button,input[type="button"],input[type="checkbox"],input[type="radio"],input[type="range"],input[type="submit"]{cursor:pointer}button::-moz-focus-inner{padding:0;border:0}a,a:visited{text-decoration:none}a:hover{text-decoration:none}ul,li,ol{list-style:none}table{border-collapse:collapse;border-spacing:0}h1,h2,h3,h4,h5,h6{font-size:inherit;font-weight:400}address{font-style:normal}@media (prefers-reduced-motion: reduce){*,*:before,*:after{animation-duration:0.01ms !important;animation-iteration-count:1 !important;transition-duration:0.01ms !important;scroll-behavior:auto !important}}:root{font-family:"Roboto", sans-serif}body{line-height:1.5}

.container{margin:0 auto;padding:0 0.75rem}.main__container{padding-top:2rem;padding-bottom:2rem;max-width:992px}.title{font-family:"Dancing Script", cursive;font-weight:700;font-size:3.5rem;color:#f08080}.btn{display:inline-block;padding:0.6em 0.5em;color:white;background-color:plum;border-radius:0.25em;transition:.3s}.btn:hover{background-color:#8a2be2}.btn:active{transform:translateY(5%)}.wrapper{min-height:100vh}.navbar{background-color:palevioletred}.navbar__container{max-width:992px;min-height:80px;display:flex;align-items:center;justify-content:space-between}.navbar__brand{display:flex;align-items:center;font-size:2.5rem;font-family:"Dancing Script", cursive;font-weight:700;color:white;transition:.3s}.navbar__brand:hover{color:aquamarine}.navbar__brand:hover .navbar__svg{fill:aquamarine}.navbar__svg-layout{margin-right:0.1em;height:64px;width:64px;display:flex;align-items:center;justify-content:center;border-radius:50%;background-color:plum}@media only all and (min-width: 480px){.navbar__svg-layout{margin-right:0.5em}}.navbar__svg{width:60%;height:60%;fill:white;transition:.3s}.nav__list{display:flex;align-items:center}.nav__item{margin-right:1em}.nav__link{font-size:1.2rem;color:white;transition:0.3s}.nav__link:hover{color:purple}

.blogs__item{margin-bottom:1em}.blogs__link{display:block;padding:0.75rem;font-size:1.5rem;background-color:aquamarine;border-radius:0.75rem;transition:0.3s}.blogs__link:hover{background-color:aqua}.blogs__title{margin-bottom:0.5rem;color:#f08080;font-size:2.5rem;font-family:"Dancing Script", cursive;transition:0.3s}.blogs__time{transition:0.3s;font-size:1.25rem;color:#8a2be2}

.blog__header{margin-bottom:1rem;text-align:center}.blog__title{margin-bottom:0.1em}.blog__time{font-style:italic}.blog__text{margin-bottom:0.25em;font-size:1.5rem;text-align:justify}.blog__author{text-align:right;font-style:italic;color:rebeccapurple;margin-bottom:1em}

.error{text-align:center}.error__text{font-size:1.5rem}.error__link{font-size:1.5rem;text-decoration:underline;color:#8a2be2}.error__link:hover{color:#f08080;text-decoration:underline}

.create{text-align:center}.create__title{margin-bottom:0.2em}.create__form{padding:0;max-width:768px;display:flex;flex-direction:column;align-items:center}.create__label{font-family:"Dancing Script", cursive;font-size:2rem}.create__label._wide{width:100%}.create__field,.create__area{padding:0.6em 0.5em;margin-top:0.5em;margin-bottom:1em;font-size:1.4rem;font-family:"Roboto", sans-serif;background-color:whitesmoke;border-radius:0.25em}.create__field:focus,.create__area:focus{border:thin solid #f08080;box-shadow:#f08080 0 0 0.25em}.create__field{text-align:center}.create__area{width:100%;resize:none;height:12em}.create__btn{font-size:1.5rem}.create__btn:focus-visible{border:thin solid #f08080;box-shadow:#f08080 0 0 0.25em}

